#taggbox_main *,
#taggbox_main *::before,
#taggbox_main *::after,
#tag_main_root__ *,
#tag_main_root__ *::before,
#tag_main_root__ *::after  {
  box-sizing: border-box;
}

.taggbox{
  overflow: auto;
}
.tagembed-container{
  overflow: auto;
  margin: 0 auto;
}

.tb_app_container *,
.tb_app_container *::before,
.tb_app_container *::after {
  box-sizing: border-box;
}

.tb_app_container{
  width: 100%;
}

.tb_app_container a{
  text-decoration: none !important;
}

.alert-enter {
  opacity: 0;
  transform: scale(0.9);
}
.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}
.tb_text_hashtag {
    display: inline;
}

.tb_app_container [tabindex="-1"]:focus, .tb_app_container [tabindex="-1"]:hover, .tb_app_container [tabindex="-1"]:active {
  outline: 0;
  box-shadow: none;
}

.tb_theme_container .splide__slide:before,
.tb_theme_container .splide__slide:after{
  display: none;
}

.tb_wall_container{
  height: var(--tb-theme-height);
  overflow: hidden;
}

.tb_f_s_btn {
  display: flex;
  align-items: center;
  color: #fff;
  border-radius: 3px;
  position: fixed;
  font-size: 14px;
  top: 15px;
  right: 15px;
  z-index: 999999999;
  text-decoration: none;
  padding: 14px 16px;
  background: #f2545b;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.tb_f_s_btn .tb__icon{
  font-size: 18px;
  margin-right: 8px;
}
.tb_theme_title{
  font-size: var(--tb-t-font-size);
  font-family: var(--tb-t-font-family);
  font-weight: var(--tb-t-font-weight);
  color: var(--tb-t-color);
  text-align: var(--tb-t-text-align);
  margin-bottom: 20px;
}

.tb_theme_sub_title {
  font-size: var(--tb-st-font-size);
  font-family: var(--tb-st-font-family);
  font-weight: var(--tb-st-font-weight);
  color: var(--tb-st-color);
  text-align: var(--tb-st-text-align);
  margin-bottom: 20px;
}
#tb_marquee{
  overflow: hidden;
}

.adsbox{
  display: none !important;
}
@import "./scss/IconDefaultColor.scss";

@import "./scss/SocialIcons.scss";

@import "./app/Components/scss/Component.scss";

@import "./app/Themes/scss/Themes.scss";